/* Section Sản Phẩm */
.products-section {
    margin-top: 20px;
    padding-bottom: 50px;
}

.section-header-title {
    background: white;
    color: #ee4d2d;
    text-align: center;
    padding: 15px;
    font-weight: bold;
    border-bottom: 4px solid #ee4d2d;
    margin-bottom: 10px;
    text-transform: uppercase;
}

.product-grid {
    display: grid;
    grid-template-columns: repeat(6, 1fr); /* 6 sản phẩm 1 hàng */
    gap: 5px;
}

/* Khung sản phẩm */
.product-card {
    width: 190px;
    background: #fff;
    border: 1px solid transparent;
    position: relative;
    display: flex;
    flex-direction: column;
    transition: all 0.1s ease;
    cursor: pointer;
}

/* Ảnh sản phẩm w h 190px */
.product-img-wrapper {
    width: 188px;
    height: 190px;
    position: relative;
    overflow: hidden;
}

.product-img-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Badge đè lên ảnh */
.badge-container {
    position: absolute;
    bottom: 5px;
    left: 5px;
    display: flex;
    gap: 3px;
    z-index: 2;
}

.badge {
    font-size: 10px;
    padding: 2px 4px;
    font-weight: bold;
    color: #fff;
    border-radius: 2px;
}

.badge-date { background-color: #ee4d2d; }
.badge-voucher { background-color: #ff9d00; }

/* Thông tin sản phẩm */
.product-info {
    padding: 8px;
    flex-grow: 1;
}

.product-name {
    font-size: 12px;
    line-height: 14px;
    height: 28px;
    overflow: hidden;
    margin-bottom: 8px;
    color: #333;
}

.label-love {
    background: #ee4d2d;
    color: #fff;
    font-size: 10px;
    padding: 0 2px;
    border-radius: 2px;
}

.product-price-row {
    display: flex;
    align-items: baseline;
}

.currency { color: #ee4d2d; font-size: 12px; }
.price { color: #ee4d2d; font-size: 16px; flex-grow: 1; }
.sold { color: rgba(0,0,0,.54); font-size: 11px; }

/* Nút C2 - Tìm sản phẩm tương tự */
.btn-similar {
    display: none; /* Mặc định ẩn */
    background-color: var(--bg); /* Màu cam Shopee */
    color: #fff;
    text-align: center;
    padding: 8px 0;
    text-decoration: none;
    font-size: 13px;
    width: 100%;
    border: 1px solid var(--bg); /* Viền cam đậm */
}

/* --- HIỆU ỨNG HOVER --- */
.product-card:hover {
    border: 1px solid var(--bg); /* Hiện border cam đậm hơn header */
    box-shadow: 0 1px 20px rgba(0,0,0,0.1);
    z-index: 10;
}

.product-card:hover .btn-similar {
    display: block; /* Hiện nút khi hover */
    position: absolute;
    bottom: -33px; /* Đẩy xuống dưới đáy khung hoặc tùy chỉnh để đè */
    left: -1px; /* Khớp với border của card */
    width: calc(100% + 2px);
}


/* Response cho Phone (Dưới 768px) */
@media (max-width: 768px) {
    .product-grid {
        grid-template-columns: repeat(2, 1fr); /* Chuyển thành 2 cột */
        gap: 4px; /* Tăng khoảng cách một chút cho dễ nhìn trên màn hình nhỏ */
        padding: 0 5px;
    }

    .product-card {
        width: 100%; /* Để card tự co giãn theo cột của Grid */
    }

    .product-img-wrapper {
        width: 100%; /* Ảnh tự tràn theo chiều rộng của cột */
        height: auto; 
        aspect-ratio: 1/1; /* Giữ ảnh luôn là hình vuông */
    }

    /* Trên mobile có thể luôn hiện nút C2 hoặc ẩn hẳn tùy bạn, 
       nhưng thường sẽ ẩn hover vì mobile dùng cảm ứng */
    .product-card:hover .btn-similar {
        display: none; /* Mobile không có thao tác hover chuột */
    }
}

.product-btn-buy-now-add-to-cart {
    padding: 8px;

    .btn-buy-now-add-to-cart {
        padding: 8px;
        font-size: 12px;
        border-radius: 4px;
        border: none;
        cursor: pointer;
        background: var(--bg);
        color: white;
        transition: all 0.1s ease;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .btn-buy-now-add-to-cart:hover {
        background: #d43d1a;
        // scalse 0.4
        transform: scale(1.05);
    }
}
