:root {
    --cart-item-hover-bg: #ebe1d6;
    --cart-item-hover-bg-when-checkbox: #bee69f;
}



.cart-icon {
    position: relative;
    padding: 10px;
    cursor: pointer;
}

.cart-dropdown {
    display: none; /* Mặc định ẩn */
    position: absolute;
    top: 100%;
    right: 0;
    width: 900px; /* Độ rộng khung pop-up */
    background: white;
    border: 1px solid #dbdbdb;
    box-shadow: 0 1px 3.125rem 0 rgba(0,0,0,.2);
    border-radius: 2px;
    z-index: 100;

    overflow-y: auto;        /* Hiện thanh cuộn khi nội dung vượt quá 500px */
    overflow-x: hidden;      /* Ẩn thanh cuộn ngang nếu không cần thiết */
    overflow: visible; 
    /* Tùy chọn: Thêm padding để nội dung không sát mép thanh cuộn */
    //padding-right: 5px; 

}
@media (min-width: 768px) {
    .cart-dropdown {
        max-height: 500px;
    }
}

/* Hiệu ứng Hover hiện khung */
.cart-icon:hover .cart-dropdown {
    display: block;
}

/* Mũi tên nhọn phía trên khung trắng */
.cart-dropdown::before {
    // content: "";
    // border-width: 10px 15px;
    // border-style: solid;
    // border-color: transparent transparent white transparent;
    // position: absolute;
    // right: 5px;
    // top: -18px;
    // background: var(--bg);
}

.cart-empty {
    text-align: center;
    color: #333;
}

.cart-empty img {
    width: 100px;
    margin-bottom: 10px;
}

/* Container của danh sách trong Cart */
.cart-header {
    padding: 10px;
    color: #999;
    font-size: 14px;
    background: #fafafa;
}

.cart-list-items {
    max-height: 400px;
    overflow-y: auto;
}

.cart-item {
    padding: 10px;
    border-bottom: 1px solid #e8e2e2;
     border-right: 1px solid #e8e2e2;
    width: 400px;
}




.cart-item:hover { background-color: var(--cart-item-hover-bg); }

.cart-item-img {
    width: 40px;
    height: 40px;
    border: 1px solid #e8e8e8;
    object-fit: cover;
}

.cart-item-info {
    flex: 1;
    margin: 0 10px;
    overflow: hidden;
}

.cart-item-name {
    font-size: 14px;
    color: #333;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
}

/* Khu vực giá và nút tăng giảm nằm bên phải */
.cart-item-price-section {
    text-align: right;
    display: flex;
    align-items: center;
    gap: 5px;
}

.cart-item-price {
    color: var(--bg);
    font-size: 14px;
}

/* Bộ nút C4 */
.quantity-control-c4 {
    display: flex;
    border: 1px solid #ccc;
    border-radius: 2px;
    overflow: hidden;
}

.btn-qty {
    background: white;
    border: none;
    width: 25px;
    height: 25px;
    cursor: pointer;
    font-size: 16px;
    color: #555;
}

.btn-qty:hover { background: #f5f5f5; }

.input-qty {
    width: 30px;
    height: 25px;
    border: none;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    text-align: center;
    font-size: 13px;
    outline: none;
}

.cart-footer {
    background-color: #fff;
    height: 40px; /* Thay đổi theo chiều cao thực tế của bạn */
    border-top: 1px solid #eee;
}
.btn-view-cart {
    background: #ee4d2d;
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 2px;
    cursor: pointer;
}



/* Khi .cart-item có chứa checkbox đã được check */
.cart-item:has(.cart-item-checkbox:checked) {
    background-color:  var(--cart-item-hover-bg-when-checkbox);
}


/* Mobile Response */
@media (max-width: 768px) {


    .cart-list-items {
        max-height: none;
        flex: 1;
    }

    .cart-item {
        padding: 15px 10px;
    }

    .cart-item-name {
        white-space: normal; /* Cho phép rớt dòng trên mobile cho dễ đọc */
        font-size: 13px;
    }
}


.mobile-cart-header {
   // position: fixed;
    //top: 0;
   // left: 0;
    //width: 100%;
   // z-index: 1000;
    background-color: #fff; /* Đảm bảo có màu nền để không lộ nội dung bên dưới */
    height: 50px; /* Thay đổi theo chiều cao thực tế của bạn */
    display: flex;
    align-items: center;
    border-bottom: 1px solid #eee;
    background: var(--bg);
    display: flex;
    justify-content: space-between;
}

.cart-item-select-action {
    margin-left: 15px;
}

/* Mobile Response */
@media (max-width: 768px) {
    /* Ẩn tiêu đề PC khi ở mobile */
    .cart-header-pc { display: none; }

    .cart-dropdown {
        position: fixed;
        top: 0;
        left: 100%; /* Mặc định đẩy sang phải để ẩn */
        width: 100vw;
        height: 100vh;
        background: white;
        z-index: 9999;
        transition: left 0.3s ease; /* Hiệu ứng trượt vào */
        display: flex;
        flex-direction: column;
    }

    /* Khi có class active thì hiện ra */
    .cart-dropdown.active {
        left: 0;
    }



    .btn-close-cart {
        background: none;
        border: none;
        color: white;
        font-size: 24px;
        cursor: pointer;
    }
}


