

.modal-dialog-top-middle {
  max-width: 100%;
  width: auto !important;
  display: inline-block;
}

.modal-dialog-center {
  max-width: 100%;
  width: auto !important;
  display: flex;
}








// Margin utilities
$spacing-unit: 4px;

// Margin Right
.mr-1 { margin-right: $spacing-unit * 1; }
.mr-2 { margin-right: $spacing-unit * 2; }
.mr-3 { margin-right: $spacing-unit * 3; }
.mr-4 { margin-right: $spacing-unit * 4; }

// Margin Left
.ml-1 { margin-left: $spacing-unit * 1; }
.ml-2 { margin-left: $spacing-unit * 2; }
.ml-3 { margin-left: $spacing-unit * 3; }
.ml-4 { margin-left: $spacing-unit * 4; }

// Padding Right
.pr-1 { padding-right: $spacing-unit * 1; }
.pr-2 { padding-right: $spacing-unit * 2; }
.pr-3 { padding-right: $spacing-unit * 3; }
.pr-4 { padding-right: $spacing-unit * 4; }

// Padding Top
.pt-1 { padding-top: $spacing-unit * 1; }
.pt-2 { padding-top: $spacing-unit * 2; }
.pt-3 { padding-top: $spacing-unit * 3; }
.pt-4 { padding-top: $spacing-unit * 4; }

// Padding Bottom
.pb-1 { padding-bottom: $spacing-unit * 1; }
.pb-2 { padding-bottom: $spacing-unit * 2; }
.pb-3 { padding-bottom: $spacing-unit * 3; }
.pb-4 { padding-bottom: $spacing-unit * 4; }

// Padding Left
.pl-1 { padding-left: $spacing-unit * 1; }
.pl-2 { padding-left: $spacing-unit * 2; }
.pl-3 { padding-left: $spacing-unit * 3; }
.pl-4 { padding-left: $spacing-unit * 4; }

.w100 { width: 100%; }
.h100 { height: 100%; }


.d-none {
    display: none !important;
}

.invisible {
    visibility: hidden !important;
}

// Flexbox utilities
.d-flex { display: flex; }
.d-flex-a-c { display: flex; align-items: center; }
.d-flex-a-fs { display: flex; align-items: flex-start; }
.d-flex-a-e { display: flex; align-items: end; }
.d-flex-a-s { display: flex; align-items: stretch; }

.d-flex-j-c { display: flex; justify-content: center; }
.d-flex-j-e { display: flex; justify-content: end; }
.d-flex-j-s { display: flex; justify-content: start; }
.d-flex-j-sb { display: flex; justify-content: space-between; }
.d-flex-j-sa { display: flex; justify-content: space-around; }
.d-flex-j-se { display: flex; justify-content: space-evenly; }

.d-flex-c { display: flex; align-items: center; justify-content: center; }
.d-flex-column { display: flex; flex-direction: column; }
.d-flex-row { display: flex; flex-direction: row; }
.text-center,.text-c { text-align: center; }
.text-start,.text-s { text-align: left; }
.text-end,.text-e { text-align: right; }

.table-border {
    border: 1px solid #ccc;
    border-collapse: collapse;
    
    td, th {
        border: 1px solid #ccc;
        padding: 3px;
    }
}